
import Masker from 'vux/src/components/masker/index.vue'

export default {
data () {
return {
list: [{
title: '',
desc: '',
target: 'input',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: 'MarkDown',
desc: 'markdown',
target: 'markdown',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: '',
desc: '',
target: 'voice',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: '',
desc: '',
target: 'scan',
isInput: true,
img: require('@/assets/note-add-scan.jpg')
}, {
title: '',
desc: '',
target: 'uploadImg',
isInput: true,
img: require('@/assets/note-add-upload.png')
}]
}
},
methods: {
onClickMask (item) {
!item.isInput && this.$router.push(`/note/add/${item.target}`)
}
},
components: {
Masker
}
}



